import {Component} from 'react';
import Posts from '../MyPosts';
import ProfileInfo from '../ProfileInfo/profileInfo';
import Loading from '../../../common/loading/loading';

class Profile extends Component {
    componentDidMount() {
        const props = this.props;
        const {setProfile, setStatus, match} = props;
        
        let userId = match.params.userId;
        if (!userId) {
            userId = props.userId;
        }
        setProfile(userId);
        setStatus(userId);
    }
    render () {
        const props = this.props;
        const { addPost, changePostText, newPostText, profile, posts } = props; /* profile, posts */
        const {status, editMode, toggleEditMode, newStatusText, updateStatus} = props; /* status */
        
        if (!profile) {
            return (<Loading/>)
        }
        return (
            <div>
                <ProfileInfo addPost={addPost} changePostText={changePostText} newPostText={newPostText} profile={profile} status={status} statusEditMode={editMode} toggleEditModeStatus={toggleEditMode} newStatusText={newStatusText} updateStatus={updateStatus}/>
                <Posts posts={posts} profile={profile} />
            </div>
        )
    }
}
export default Profile;